<template>
  <div class="customer-page">
    <!-- 標題 -->
    <h1>客戶資料</h1>

    <!-- 新增客戶表單 -->
    <div class="form-section">
      <h2>新增客戶</h2>
      <form @submit.prevent="submitNewCustomer" class="form">
        <div class="form-group">
          <label for="name">名稱</label>
          <input type="text" id="name" v-model="newCustomer.name" required />
        </div>
        <div class="form-group">
          <label for="email">電子郵件</label>
          <input type="email" id="email" v-model="newCustomer.email" required />
        </div>
        <button type="submit" class="btn btn-primary">新增客戶</button>
      </form>
    </div>

    <!-- 客戶表格 -->
    <div class="table-section">
      <table class="customer-table">
        <thead>
          <tr>
            <th>客戶名稱</th>
            <th>電子郵件</th>
            <th>去年總交易金額</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="customer in customers" :key="customer.id">
            <td>{{ customer.name }}</td>
            <td>{{ customer.email }}</td>
            <td>{{ customer.total_amount_last_year }}</td>
            <td>
              <button @click="editCustomer(customer)" class="btn btn-secondary">
                編輯
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- 編輯客戶表單 -->
    <div v-if="editingCustomer" class="form-section">
      <h3>編輯客戶</h3>
      <form @submit.prevent="submitUpdate" class="form">
        <div class="form-group">
          <label for="editName">名稱</label>
          <input
            type="text"
            id="editName"
            v-model="editingCustomer.name"
            required
          />
        </div>
        <div class="form-group">
          <label for="editEmail">電子郵件</label>
          <input
            type="email"
            id="editEmail"
            v-model="editingCustomer.email"
            required
          />
        </div>
        <button type="submit" class="btn btn-primary">更新資料</button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      customers: [],
      newCustomer: {
        name: "",
        email: "",
      },
      editingCustomer: null,
      isUpdating: false,
    };
  },
  mounted() {
    this.fetchCustomers();
  },
  methods: {
    async fetchCustomers() {
      try {
        const response = await axios.get("http://localhost:8888/customers");
        this.customers = response.data;
      } catch (error) {
        console.error("無法取得客戶資料", error);
      }
    },

    async submitNewCustomer() {
      try {
        await axios.post("http://localhost:8888/customers", this.newCustomer);
        alert("新增成功！");
        this.fetchCustomers();
        this.newCustomer = { name: "", email: "" };
      } catch (error) {
        console.error("新增失敗", error);
      }
    },

    editCustomer(customer) {
      this.editingCustomer = { ...customer };
    },

    async submitUpdate() {
      this.isUpdating = true;
      try {
        await axios.put(
          `http://localhost:8888/customers/${this.editingCustomer.id}`,
          this.editingCustomer
        );
        await this.fetchCustomers();
        alert("更新成功！");
        this.editingCustomer = null;
      } catch (error) {
        console.error("更新失敗", error);
      } finally {
        this.isUpdating = false;
      }
    },
  },
};
</script>

