<template>
  <div>
    <h1>客戶資料</h1>

    <!-- 交易紀錄查詢表單 -->
    <h2>查詢客戶交易紀錄</h2>
    <form @submit.prevent="fetchCustomerTransactions">
      <div>
        <label for="customerSelect">選擇客戶</label>
        <select v-model="selectedCustomerId" id="customerSelect" required>
          <option v-for="customer in customers" :key="customer.id" :value="customer.id">
            {{ customer.name }}
          </option>
        </select>
      </div>
      <div>
        <label for="startDate">開始日期</label>
        <input type="date" id="startDate" v-model="startDate" required />
      </div>
      <div>
        <label for="endDate">結束日期</label>
        <input type="date" id="endDate" v-model="endDate" required />
      </div>
      <button type="submit">查詢交易紀錄</button>
    </form>

    <!-- 顯示交易紀錄 -->
    <table v-if="transactions.length > 0">
      <thead>
        <tr>
          <th>客戶</th>
          <th>交易日期</th>
          <th>金額</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="transaction in transactions" :key="transaction.id">
          <td>{{ transaction.id }}</td>
          <td>{{ transaction.date }}</td>
          <td>{{ transaction.amount }}</td>
        </tr>
      </tbody>
    </table>

    <p v-else>尚未查詢到交易紀錄</p>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      customers: [],
      selectedCustomerId: null,
      startDate: '',
      endDate: '',
      transactions: [],
    };
  },
  mounted() {
    this.fetchCustomers();
  },
  methods: {
    // 獲取所有客戶資料
    async fetchCustomers() {
      try {
        const response = await axios.get('http://localhost:8888/customers');
        this.customers = response.data;
      } catch (error) {
        console.error('無法取得客戶資料', error);
      }
    },

    // 查詢指定客戶及時間範圍的交易紀錄
    async fetchCustomerTransactions() {
      try {
        const response = await axios.get(`http://localhost:8888/transactions/${this.selectedCustomerId}`, {
          params: {
            startDate: this.startDate,
            endDate: this.endDate,
          },
        });
        this.transactions = response.data; // 使用正確格式解析資料
      } catch (error) {
        alert(`查詢失敗: ${error.response?.data?.error || '伺服器錯誤'}`);
        console.error('查詢交易紀錄失敗', error);
      }
    },
  },
};
</script>
